import { Quote } from "lucide-react";
import React, { useState } from "react";
import SliderTestimonials from "./SlideTestimonials";
import Masonry, { ResponsiveMasonry } from "react-responsive-masonry";
import fallBack_image from "../../assets/Images/user.png"
import {
  testimonials,
  testimonialsContainer,
  testimonialsHeader,
  testimonialsTag,
  testimonialsCarousel,
  testimonialsGrid,
  testimonialGridItem,
  testimonialText,
  testimonialAuthor,
  quoteMark,
  authorImage,
  authorInfo,
} from "../../Styles/testimonials.module.css";

export const TestimonialCards = ({ testimonialsData }) => {
  return (
    <>
      <div className={`${testimonialsGrid} container`}>
        <ResponsiveMasonry columnsCountBreakPoints={{ 350: 1, 750: 2, 900: 3 }}>
          <Masonry>
            {testimonialsData.map((testimonial) => (
              <div key={testimonial.id} className={testimonialGridItem}>
                <img
                  className={quoteMark}
                  src="https://askproject.net/finest/wp-content/uploads/sites/105/2022/10/quote_Asset-4.png"
                  alt=""
                />
                <p className={testimonialText}>{testimonial.text}</p>
                <div className={testimonialAuthor}>
                  <img
                    src={
                      testimonial.image?.trim()
                        ? testimonial.image
                        : fallBack_image
                    }
                    alt={testimonial.author}
                    className={authorImage}
                  />
                  <div className={authorInfo}>
                    <h4>{testimonial.author}</h4>
                    <p>{testimonial.position}</p>
                  </div>
                </div>
              </div>
            ))}
          </Masonry>
        </ResponsiveMasonry>
      </div>
    </>
  );
};

const Testimonials = ({ testimonials: testimonialsData }) => {
  return (
    <section className={testimonials}>
      <div className={testimonialsContainer}>
        <div className={testimonialsHeader}>
          <span className={testimonialsTag}>TESTIMONIALS</span>
          <h2>Reviews From Our Clients</h2>
        </div>

        <div className={testimonialsCarousel}>
          <SliderTestimonials testimonials={testimonialsData} />
        </div>
      </div>
      <TestimonialCards testimonialsData={testimonialsData} />
    </section>
  );
};

export default Testimonials;
